#main-cover {
  height: 100vh;
  box-sizing: border-box;
}

#main-cover #main-cover-image {
  position: absolute;
  background-image: url(../../uploads/home_cover.jpg);
  background-position: center center;
  background-repeat: no-repeat;
  background-size: cover;
  z-index: 1;
  width: 100%;
  height: 100%;
  opacity: 0;
  visibility: hidden;
}

@media screen and (max-width: 720px) {
  #main-cover #main-cover-image {
    background-position: center bottom;
    background-image: url(../../uploads/home_cover_sp.jpg);
  }
}

#main-cover #main-cover-image.active:after {
  transform: opacity 0.6s;
  opacity: 1;
}

#main-cover #cover-bg {
  transition: opacity 0.6s ease-in;
  position: absolute;
  content: '';
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  background-color: rgba(0, 0, 0, 0.4);
  z-index: 2;
  opacity: 0;
}

#main-cover #main-cover-image.active + #cover-bg {
  opacity: 1;
}

#main-cover .cover-content {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 3;
}

@media screen and (max-width: 720px) {
  #main-cover .cover-content {
    width: 100%;
    text-align: center;
    top: 35%;
    left: 0;
    transform: translate(0, 0);
  }
}

#main-cover .cover-content h1 {
  display: none;
  white-space: nowrap;
  font-size: 3.2rem;
  font-weight: bold;
  text-align: center;
  color: #5f5e5e;
}

@media screen and (max-width: 720px) {
  #main-cover .cover-content h1 {
    font-size: 1.7rem;
  }
}

#main-cover .scroll {
  position: absolute;
  width: 100%;
  text-align: center;
  bottom: 2em;
  visibility: hidden;
  text-align: center;
  font-size: 1.6rem;
  line-height: 1;
  z-index: 3;
}

#main-cover .scroll a {
  color: #FFF;
  animation: blink .5s ease-in-out infinite alternate;
}

#main-cover .scroll a span {
  display: inline-block;
  vertical-align: middle;
}

#main-cover .scroll a span.icon {
  margin-left: 4px;
}

@media screen and (max-width: 720px) {
  #main-cover .scroll {
    font-size: 1.2rem;
  }
}

@keyframes blink {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

